"use client"

import * as React from "react"
import { Input } from "@/components/ui/input"
import { cn } from "@/lib/utils"

interface PhoneInputProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, "onChange" | "value"> {
  value?: string
  onChange?: (value: string) => void
  defaultCountry?: string
}

// Список популярных стран с кодами
const COUNTRIES = [
  { code: "RU", dialCode: "+7", name: "Россия", mask: "+7 (999) 999-99-99" },
  { code: "BY", dialCode: "+375", name: "Беларусь", mask: "+375 (99) 999-99-99" },
  { code: "KZ", dialCode: "+7", name: "Казахстан", mask: "+7 (999) 999-99-99" },
  { code: "UA", dialCode: "+380", name: "Украина", mask: "+380 (99) 999-99-99" },
  { code: "US", dialCode: "+1", name: "США", mask: "+1 (999) 999-99-99" },
  { code: "GB", dialCode: "+44", name: "Великобритания", mask: "+44 9999 999999" },
]

export function PhoneInput({ value = "", onChange, defaultCountry = "RU", className, ...props }: PhoneInputProps) {
  const [selectedCountry, setSelectedCountry] = React.useState(
    COUNTRIES.find((c) => c.code === defaultCountry) || COUNTRIES[0]
  )

  // Определяем код страны из значения
  React.useEffect(() => {
    if (value) {
      // Сортируем страны по длине кода (от большего к меньшему) для правильного определения
      const sortedCountries = [...COUNTRIES].sort((a, b) => {
        return b.dialCode.replace(/\D/g, "").length - a.dialCode.replace(/\D/g, "").length
      })
      
      const detectedCountry = sortedCountries.find((country) => {
        const countryDigits = country.dialCode.replace(/\D/g, "")
        const valueDigits = value.replace(/\D/g, "")
        return valueDigits.startsWith(countryDigits)
      })
      
      if (detectedCountry && detectedCountry.code !== selectedCountry.code) {
        setSelectedCountry(detectedCountry)
      }
    }
  }, [value, selectedCountry.code])

  // Форматирование номера
  const formatPhoneNumber = (allDigits: string): string => {
    const countryDigits = selectedCountry.dialCode.replace(/\D/g, "")
    
    // Если введено меньше или равно цифр коду страны, возвращаем код страны
    if (allDigits.length <= countryDigits.length) {
      return selectedCountry.dialCode
    }
    
    // Если номер начинается с кода страны, убираем его для форматирования
    let numberPart = allDigits
    if (allDigits.startsWith(countryDigits)) {
      numberPart = allDigits.slice(countryDigits.length)
    } else {
      // Если не начинается с кода страны, возвращаем как есть
      return `+${allDigits}`
    }

    // Форматируем в зависимости от страны
    if (selectedCountry.code === "RU" || selectedCountry.code === "KZ") {
      // +7 (999) 999-99-99
      if (numberPart.length <= 3) {
        return `${selectedCountry.dialCode} (${numberPart}`
      }
      if (numberPart.length <= 6) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 3)}) ${numberPart.slice(3)}`
      }
      if (numberPart.length <= 8) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 3)}) ${numberPart.slice(3, 6)}-${numberPart.slice(6)}`
      }
      return `${selectedCountry.dialCode} (${numberPart.slice(0, 3)}) ${numberPart.slice(3, 6)}-${numberPart.slice(6, 8)}-${numberPart.slice(8, 10)}`
    } else if (selectedCountry.code === "BY") {
      // +375 (99) 999-99-99
      if (numberPart.length <= 2) {
        return `${selectedCountry.dialCode} (${numberPart}`
      }
      if (numberPart.length <= 5) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2)}`
      }
      if (numberPart.length <= 7) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2, 5)}-${numberPart.slice(5)}`
      }
      return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2, 5)}-${numberPart.slice(5, 7)}-${numberPart.slice(7, 9)}`
    } else if (selectedCountry.code === "UA") {
      // +380 (99) 999-99-99
      if (numberPart.length <= 2) {
        return `${selectedCountry.dialCode} (${numberPart}`
      }
      if (numberPart.length <= 5) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2)}`
      }
      if (numberPart.length <= 7) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2, 5)}-${numberPart.slice(5)}`
      }
      return `${selectedCountry.dialCode} (${numberPart.slice(0, 2)}) ${numberPart.slice(2, 5)}-${numberPart.slice(5, 7)}-${numberPart.slice(7, 9)}`
    } else if (selectedCountry.code === "US") {
      // +1 (999) 999-99-99
      if (numberPart.length <= 3) {
        return `${selectedCountry.dialCode} (${numberPart}`
      }
      if (numberPart.length <= 6) {
        return `${selectedCountry.dialCode} (${numberPart.slice(0, 3)}) ${numberPart.slice(3)}`
      }
      return `${selectedCountry.dialCode} (${numberPart.slice(0, 3)}) ${numberPart.slice(3, 6)}-${numberPart.slice(6, 10)}`
    } else if (selectedCountry.code === "GB") {
      // +44 9999 999999
      if (numberPart.length <= 4) {
        return `${selectedCountry.dialCode} ${numberPart}`
      }
      return `${selectedCountry.dialCode} ${numberPart.slice(0, 4)} ${numberPart.slice(4, 10)}`
    }

    // По умолчанию просто добавляем код страны
    return numberPart ? `${selectedCountry.dialCode} ${numberPart}` : selectedCountry.dialCode
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const input = e.target.value
    const digits = input.replace(/\D/g, "")

    // Автоопределение страны по коду (проверяем начало номера)
    if (digits.length > 0) {
      // Сортируем страны по длине кода (от большего к меньшему) для правильного определения
      const sortedCountries = [...COUNTRIES].sort((a, b) => {
        return b.dialCode.replace(/\D/g, "").length - a.dialCode.replace(/\D/g, "").length
      })
      
      const detectedCountry = sortedCountries.find((country) => {
        const countryDigits = country.dialCode.replace(/\D/g, "")
        return digits.startsWith(countryDigits)
      })
      
      if (detectedCountry && detectedCountry.code !== selectedCountry.code) {
        setSelectedCountry(detectedCountry)
      }
    }

    // Ограничиваем длину номера (максимум 15 цифр согласно E.164)
    const maxDigits = 15
    const limitedDigits = digits.slice(0, maxDigits)

    // Форматируем номер
    const formatted = formatPhoneNumber(limitedDigits)

    if (onChange) {
      onChange(formatted)
    }
  }

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    // Разрешаем навигационные клавиши
    if (
      e.key === "Backspace" ||
      e.key === "Delete" ||
      e.key === "ArrowLeft" ||
      e.key === "ArrowRight" ||
      e.key === "Tab" ||
      e.key === "Home" ||
      e.key === "End" ||
      (e.key === "a" && (e.metaKey || e.ctrlKey)) || // Select all
      (e.key === "c" && (e.metaKey || e.ctrlKey)) || // Copy
      (e.key === "v" && (e.metaKey || e.ctrlKey)) || // Paste
      (e.key === "x" && (e.metaKey || e.ctrlKey))    // Cut
    ) {
      return
    }

    // Разрешаем только цифры
    if (!/^\d$/.test(e.key)) {
      e.preventDefault()
    }
  }

  const displayValue = value || selectedCountry.dialCode

  return (
    <Input
      type="tel"
      value={displayValue}
      onChange={handleChange}
      onKeyDown={handleKeyDown}
      placeholder={selectedCountry.mask}
      className={className}
      {...props}
    />
  )
}
